import React from 'react'
import styled from 'styled-components'
import Slider from 'react-slick'
import {Grid, Row, Col} from 'react-flexbox-grid'
import {
  Card,
  CardActions,
  CardHeader,
  CardMedia,
  CardTitle,
  CardText
} from 'material-ui/Card';
import FlatButton from 'material-ui/FlatButton';
import RaisedButton from 'material-ui/RaisedButton';
import DropDownMenu from 'material-ui/DropDownMenu';
import MenuItem from 'material-ui/MenuItem';

const Wrapper = styled.div `

margin-bottom:20px;
padding-bottom:30px;

.space{
  margin: 4px;
}

.slick-next:before, .slick-prev:before {
  font-size: 20px;
  line-height: 1;
  opacity: .75;
  color: grey;
}

`
class FileCarousel extends React.Component {

  constructor(props) {
    super(props)
    this.next = this.next.bind(this)
    this.previous = this.previous.bind(this)
    this.state = {
      value: 1
    }

  }

  componentDidMount() {}

  next() {
    this.slider.slickNext()
  }
  previous() {
    this.slider.slickPrev()
  }

  handleChange = (event, index, value) => this.setState({value});

  render() {

    var settings = {
      dots: true,
      arrows: false,
      infinite: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 1,
      draggable: false,
      responsive: [
        {
          breakpoint: 768,
          settings: {
            slidesToShow: 1
          }
        }, {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3
          }
        }, {
          breakpoint: 1368,
          settings: {
            slidesToShow: 4
          }
        }, {
          breakpoint: 2000,
          settings: {
            slidesToShow: 5
          }
        }, {
          breakpoint: 100000,
          settings: 'unslick'
        }
      ]
    };

    return (
      <Wrapper className="grey lighten-4">
        <Grid>
          <Row>
            <Col xs={12}>
              <div className="align-center">
                <DropDownMenu value={this.state.value} onChange={this.handleChange}>
                  <MenuItem value={1} primaryText="Fichas recientes"/>
                  <MenuItem value={2} primaryText="Fichas más vistadas"/>
                  <MenuItem value={3} primaryText="Fichas en peligro"/>
                  <MenuItem value={4} primaryText="Fichas por actualizar"/>
                </DropDownMenu>
              </div>
              <Slider ref={c => this.slider = c} {...settings}>
                {this.props.data.map((record, i) => (
                  <div key={i}>
                    <Card className="space">
                      <CardHeader title="URL Avatar" subtitle="Subtítulo" avatar="avatar4.png"/>
                      <CardMedia>
                        <img src="file.jpg"/>
                      </CardMedia>
                      <CardTitle title="Título" subtitle="Subtítulo"/> {/* <CardText>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                      </CardText> */}
                      <CardActions>
                        <FlatButton label="Action1"/>
                        <FlatButton label="Action2"/>
                      </CardActions>
                    </Card>
                  </div>
                ))}
              </Slider>

            </Col>
          </Row>
          <br/>
          <br/>
          <div style={{
            textAlign: 'center'
          }}>
            <RaisedButton label="Anterior" onTouchTap={this.previous}/>
            <RaisedButton label="Siguiente" onTouchTap={this.next}/>
          </div>
        </Grid>
      </Wrapper>
    )
  }
}

export default FileCarousel
